import React from 'react'
import { Link } from 'react-router-dom'
import { ReactComponent as Aceptable } from '../assets/0.svg'
import { ReactComponent as Bueno } from '../assets/1.svg'
import { ReactComponent as MuyBueno } from '../assets/2.svg'
import { ReactComponent as Extraordinario } from '../assets/3.svg'
import plusIcon from '../assets/plus-icon.png'

export default function BookCard({
    imgSrc,
    ISBN,
    authors,
    bookName,
    score
}) {
    // const backUrl = process.env.REACT_APP_BACK_URL
    return (
        <Link className='book-card' to={'/book/'+ISBN} target="_blank" rel="noopener noreferrer">
            <div className="viewMore">
                <img src={plusIcon} alt="" />
            </div>
            <img src={imgSrc} />
            <div className='book-card-text'>
                {authors.length > 0 &&
                    authors.map((e) => {
                        return <h3 key={e.name}>{e.name} {e.lastName ? e.lastName : ''}</h3>
                    })
                }
                <h2>{bookName}</h2>
                <div className='card-score'>
                    <p>
                        {score === 'Extraordinario' ? 
                            <Extraordinario style={{width: 30, verticalAlign: 'middle', marginRight: 10}} /> :
                        score === 'Muy bueno' ?
                            <MuyBueno style={{width: 30, verticalAlign: 'middle', marginRight: 10}} /> :
                        score === 'Bueno' ?
                            <Bueno style={{width: 30, verticalAlign: 'middle', marginRight: 10}} /> :
                            <Aceptable style={{width: 30, verticalAlign: 'middle', marginRight: 10}} />
                        }
                        {score}
                    </p>
                </div>
            </div>
        </Link>
    )
}